import React from 'react';
import { Card, CardContent, Typography } from '@mui/material';

const MUEICard = () => {
  return (
    <Card className='education__card'>
      <CardContent>
        <Typography variant="h5">Master en Ingeniera Informtica</Typography>
        <Typography variant="body2">UDC (A Corua, Espaa)</Typography>
        <Typography variant="body2">2019 - 2021</Typography>
      </CardContent>
    </Card>
  );
}

export default MUEICard;